import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { Badge } from '../Badge';

describe('Test Badge component', () => {
  it('render', async function () {
    const content = render(<Badge>Badge</Badge>);
    const initials = await content.findByText('Badge');
    expect(initials).not.null;
  });

  it('variant property success', async function () {
    const content = render(<Badge variant="success">Badge</Badge>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(3);
  });

  it('variant property danger', async function () {
    const content = render(<Badge variant="danger">Badge</Badge>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(3);
  });

  it('variant property warning', async function () {
    const content = render(<Badge variant="warning">Badge</Badge>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(3);
  });

  it('variant property info', async function () {
    const content = render(<Badge variant="info">Badge</Badge>);
    const firstElement = content.container.firstElementChild;
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(3);
  });
});
